.menuHeader{
    width: 100%;
    height: 40px;
    background: #fff;
    border-bottom: 1px solid #cecece;
    box-shadow: 0 2px 2px -2px #333;
    margin-left: 20%;
}
.menuHeader ul{
    list-style: none;
    margin: 0;
    padding: 0;
}
.menuHeader > ul{
    display: flex;
    margin: 0 50px;
    position: relative;
}
.menuHeader > ul > li > span{
    display: block;
    line-height: 40px;
    cursor: pointer;
    padding: 0 20px;
    font-size: 25px;
}
.menuHeader > ul > li > span:hover{
    color: #3153e9;
    font-size: 28px;
    
}
.menuHeader > ul > li > ul{
    position: absolute;
    background: #fff;
    left: 0;
    right: 0;
    display: none;
    border: 1px solid #cecece;
    z-index: 1;
}
.menuHeader > ul > li:hover ul{
    display: block;
}
.menuHeader > ul > li > ul > li{
    margin: 0 20px;
    min-width: 150px;
}
.menuHeader > ul > li > ul > li{
    float: left;
}
.menuHeader > ul > li > ul > li > a{
    font-weight: bold;
    display: block;
}
.menuHeader > ul > li > ul > li a{
    padding: 3px 0;
    display: block;
    font-size: 25px;
    text-decoration: none;
    color: #000000;
}